@import 'globals';
@import "pl-loading";
p {
    color: #333333;
}
#wpcontent {
    padding-left: 0;
}

.uh-main-page .pl-page-body {
    background-image: url(../img/main-background.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    padding: 20px;
}

.uh-main-page .pl-page-header {
    padding: 50px 0 50px 50px;
    background-image: url("../img/pat1.jpg");
    background-size: cover;
    text-transform: uppercase;
    text-align: center;
}

.uh-main-page .form-header {
    margin: 25px 0;
}

.uh-main-page .custom-switch input {
    display: none;
}

.uh-main-page .custom-switch .custom-control-label::before {
    width: 60px;
    border-radius: 50px;
    height: 30px;
}

.uh-main-page .custom-switch .custom-control-label::after {
    left: calc(-60px + 27px);
    width: 24px;
    height: 25px;
    border-radius: 50%;
}

.uh-main-page .custom-switch .custom-control-input:checked~.custom-control-label::after {
    -webkit-transform: translateX(30px);
    transform: translateX(30px);
}

.uh-main-page .custom-control-label {
    font-size: 17px;
    line-height: 2;
    padding-left: 35px;
    margin-bottom: 10px;
}

